import React, {Component} from "react"
import uuidv1 from 'uuid/v1'

import { Animation, Container, Footer, Layout, SEO, Spacer } from "../../../components"
import Swatch from "./components/swatch"
import Chip from "./components/chip"
import contraststyle from "./styles/contrast.module.css"
import "./styles/contrast-global.css"

export default class Contrast extends Component {

  constructor(props) {
    super(props);
    this.state = {
      hexValues: '#ffffff, #3F3F3F, #1691CF, #bada33',
      hideFailed: false,
      hideRatio: true
    };

    this.handelInputChange = this.handelInputChange.bind(this);
    this.handelCheckboxFailed = this.handelCheckboxFailed.bind(this);
    this.handelCheckboxRatio = this.handelCheckboxRatio.bind(this);
    this.loadAxiom = this.loadAxiom.bind(this);
    this.loadDefault = this.loadDefault.bind(this);
  }



  handelInputChange(event){
    this.setState({hexValues: event.target.value});
  }

  loadAxiom(){
    this.setState({hexValues: "#353535, #EDEBF2, #212121, #FFFFFF, #4CA15A, #D74A4A, #BBB9C0, #F5B1A4 ,#F498B7, #E35A7A, #A72E5E, #9C73B2, #90A0D6, #6063A1, #3B79CC, #7FD6F8, #55CDE6, #24ADBD, #2F9170, #74C971, #80BE4C, #C8C819, #F0EC1D, #FCD512, #FFBE0A, #F98A39, #FF6D56, #CD811A, #9E6139, #BDA35B, #9E8E75, #BBB9C0 ,#78909C" });
  }

  loadDefault(){
    this.setState({hexValues: "#ffffff, #3F3F3F, #1691CF, #bada33" });
  }

  handelCheckboxFailed(){
    this.setState(prevState => ({
      hideFailed: !prevState.hideFailed
    }))
  }

  handelCheckboxRatio(){
    this.setState(prevState => ({
      hideRatio: !prevState.hideRatio
    }))
  }

  render() {
    let hexValuesList = this.state.hexValues.split(',')
    let sampleText = 'Text'

  return (
  <Layout>
    <div className={contraststyle.contrast}>
      <SEO title="contrast" />
        <Container>
          <Animation effect="fadeIn">
            <div className={contraststyle.header}>
              <h1>Contrast Ratio</h1><div className={contraststyle.tagalpha}>ALPHA</div>
              <h6>Understand color accessability across a set of hex values</h6>
            </div>
          </Animation>

          <Animation effect="fadeIn">
            <Spacer height='1rem'/>
            <label>Hex values</label><br/>
            <input
              type="text"
              className={contraststyle.userInputText}
              value={this.state.hexValues}
              onChange={this.handelInputChange}
              spellCheck="false"
              />
             <div className="default-options">
               <span className="default-option" onClick={this.loadAxiom}>Load Axiom Colours</span> &nbsp;
               <span className="default-option" onClick={this.loadDefault}>Load Default Colours</span>
               <Spacer height='1rem'/>
             </div>


             <div className="input-group">
               <span className="input-label">Hide failed</span>
               <input id="switch" type="checkbox" onChange={this.handelCheckboxFailed}/>
               <label htmlFor="switch" className="switch-label">Toggle</label>
             </div>

             <div className="input-group">
               <span className="input-label">Hide Ratio</span>
               <input id="switch-ratio" type="checkbox" onChange={this.handelCheckboxRatio}/>
               <label htmlFor="switch-ratio" className="switch-label">Toggle</label>
             </div>

            <Spacer height='3rem'/>
            <div className="contrast-grid" style={{
              gridTemplateColumns: `repeat(${hexValuesList.length +1}, 1fr)`
            }}>
              {/* Leave top left blank */}
              <div className="grid-static"/>
              {/* Header row & Columns */}
              {hexValuesList.map((hexValueItem, index) => {
                return (
                  <React.Fragment key={uuidv1()}>
                    <div key={uuidv1()} className="grid-header">
                      <Swatch color={hexValueItem}/>
                        <span>{hexValueItem}</span>
                    </div>
                    <div key={uuidv1()} className="grid-header" style={{gridRow: index+2}}>
                      <Swatch color={hexValueItem}/>
                        {hexValueItem}
                    </div>
                  </React.Fragment>
                )
              })}
              {/* Main colours */}
              {hexValuesList.map((hexValue1, index1) => (
                  <React.Fragment key={uuidv1()}>
                    {hexValuesList.map((hexValue2) => {
                      if(hexValue1 !== hexValue2) {
                        return (
                          <div key={uuidv1()}>
                            <Chip
                            color1={hexValue1}
                            color2={hexValue2}
                            hideFailed={this.state.hideFailed}
                            hideRatio={this.state.hideRatio}
                            >
                            {sampleText}
                            </Chip>
                          </div>
                        )
                      } else { return ( <div key={uuidv1()}/>)}
                    })}
                  </React.Fragment>
              ))}
            </div>

            <Spacer height='20rem'/>

          </Animation>
        </Container>
      </div>
    <Footer/>
  </Layout>
  )
}
}
